	      type: 'Workshop',
	      description: 'Velkommen til løsning av Ruter-case med maskinlæring: Forbedring av forventet ankomst av busser basert på reelle sanntidsdata. Hva skjer når Knowits eminente selgere lover at vi LETT kan toppe dagens sanntidsløsning hos Ruter med maskinlæring? Workshopen følger caset fra innsalg via utforsking av data til å løse case med passende modeller. Vi har på forhånd samlet noen ukers sanntidsdata fra Ruters busser i Oslo. Du lærer nok teori og metode for å løse et ekte case. Vi implementerer sammen forbedrede algoritmer i Python med velkjente, enkle open source-rammeverk.'
